<template>
  <div class="login df aic jcc">
    <el-form
      :rules="rules"
      :model="form"
      ref="ruleForm"
      hide-required-asterisk
      class="plr40 br10 login-form"
    >
      <h3>后台系统</h3>
      <el-form-item prop="user_id">
        <el-input
          autofocus
          clearable
          v-model="form.user_id"
          type="text"
          prefix-icon="el-icon-user-solid"
          placeholder="请输入账号"
          @keyup.enter.native="handleLogin"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          v-model="form.password"
          prefix-icon="el-icon-lock"
          placeholder="请输入密码"
          show-password
          @keyup.enter.native="handleLogin"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-checkbox v-model="form.checked">记住我</el-checkbox>
      </el-form-item>
      <el-form-item>
        <el-button
          class="df aic jcc login-button"
          type="primary"
          @click.native.prevent="handleLogin"
        >
          <span>登录</span>
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "login",
  data() {
    return {
      form: {
        user_id: "",
        password: "",
        checked: false //控制登录的时候是否记住我，只有登录成功了才记住我
      },
      rules: {
        user_id: [
          { required: true, message: "请输入账号", trigger: "blur" },
          { min: 5, max: 16, message: "长度在 5 到 16 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 16, message: "长度在 6 到 16 个字符", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
  },
  methods: {
    //登录功能
    login() {
      const loading = this.$loading({
        lock: true,
        text: "拼命加载中...",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.8)"
      });
      this.$post("user/login", {
        user_id: this.form.user_id,
        password: this.form.password
      })
        .then(res => {
          if (!res.user_id) {
            this.$notify({
              title: "提示",
              message: res,
              type: "warning"
            });
            loading.close();
            return;
          }
          var data = {
            password: this.form.password,
            ...res
          };
          localStorage.setItem("userInfo", JSON.stringify(res));
          loading.close();
          this.$router.replace("/page");
        })
        .catch(res => {
          loading.close();
        });
    },
    //点击登录时候触发
    handleLogin() {
      console.log(this.$refs);
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.login();
        } else {
          return valid;
        }
      });
    }
  }
};
</script>

<style>
.login {
  background: url(../../assets/img/login.jpg) no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
}
.login-form {
  background: #fff;
  width: 300px;
}
.login-form > h3 {
  text-align: center;
}
.login-button {
  width: 100%;
}
</style>


